.galleryImage {
    width: 100%;
    overflow: hidden;
  }
  
  /* Medium devices (landscape tablets, 768px and up) */


  @media only screen and (min-width: 600px) {
    .galleryImage {
        width: 100%;
      }
      
  }
  @media only screen and (min-width: 768px) {
    .galleryImage {
        width: 50%;
      }
      
  }
  @media only screen and (min-width: 992px) {
    .galleryImage {
        width: 50%;
      }
      
  }
  @media only screen and (min-width: 1200px) {
    .galleryImage {
        width: 25%;
      }
      
  }
  
.unity{
margin:auto;
  width: 100%;
  padding: 15em 0em;
  background-color: #e2e2e2;
}
/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .unity{
    padding: 15em 0em;
  }
}
@media only screen and (min-width: 768px) {
  .unity{
    padding: 12em 2em;
  }
}
@media only screen and (min-width: 992px) {
  .unity{
    width:90%;
    padding: 12em 5em;
  }
}
@media only screen and (min-width: 1200px) {
  .unity{
    width: 80%;
    padding: 12em 10em;
  }
}

.docs {
  padding: 1em;
  background-color: #e2e2e2;
}

.docs img {
  width: 400px;
  height: 400px;
  overflow: hidden;
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .docs {
    padding: 5em;
  }

  .docs img {
    max-width: 50%;
  }
}
/* */

.App {
  font-family: "Roboto", sans-serif;
}

